body {
  font-family: sans-serif;
  color: white;
  background-color: rgba(0, 0, 0, .8);
  font-size: 12px;
}

h2 {
  font-size: 0.8em;
  text-align: center;
  color: #eae9ff;
  border: solid white;
  margin: 0 5px;
  border-radius: 2px 2px 0 0;
  border-width: 1px 1px 0px 1px;
  padding: 1em 0;
  background: rgba(62, 117, 20, 0.92);
}

h3 {
  margin: 1em auto 0.1em;
  text-transform: capitalize;
}

#wrapper {
  display: table;
  background-color: rgba(0, 0, 0, .8);
  border-radius: 2px;
  filter: drop-shadow(0 0 3px black);
  width: 100%;
}

.reports {
  display: table-row;
  position: relative;
  background-color: rgba(255, 255, 255, 0.12);
  &:nth-child(2n+1) {
    background-color: rgba(255, 255, 255, 0.22);
  }
}

.report {
  padding: 0.5em;
  border-radius: 0 6px 0 0;
  margin: 1px;
  position: relative;
  display: table-cell;
  background-color: rgba(255, 255, 255, 0.12);
  &:nth-child(2n+1) {
    background-color: rgba(255, 255, 255, 0.22);
  }

  &:after {
    position: absolute;
    width: 1em;
    height: 1em;
    content: "?";
    top: 0;
    right: 0;
    border: 1px solid;
    border-radius: 50% 0 50% 50%;
    text-align: center;
    color: silver;
  }

  &.unknown-status:after {
    color: white;
  }

  &.green:after {
    background-color: green;
    border-color: green;
    color: white;

    content: "\2713";
  }

  &.red:after {
    color: white;
    background-color: red;
    border-color: red;
    content: "!!";
  }
}

.time-taken {
  text-align: center;
  padding: 1em;
  position: absolute;
  display: table-cell;
  left: 5px;
  transform: translateY(40px);
}

dt {
  text-transform: uppercase;
}

.v {
  color: green;
}

.e {
  color: red;
}

.reporter {
  position: relative;
}

.update-status {
  font-size: 0.8em;
  border: 1px solid lightskyblue;
  padding: 0.2em;
  text-align: center;
  border-radius: 2px;
  background-color: rgba(173, 216, 230, 0.34);

  opacity: 0.2;
}

.update-status:hover {
  opacity: 1;
}

.print-r {

}

.debug {
  position: relative;
  padding: 0.5em;
  .debug-toggle {
    color: rgba(255, 255, 255, 0.1);
    position: absolute;
    top: -34px;
    right: 15px;
    border: 1px solid;
    border-radius: 50%;
    padding: 0 4px 1px 4px;
  }
  .nice_r {
    display: none;
    background: white;
  }

  .debug-show & {
    .nice_r {
      display: block;
    }
  }
}

.drush {

  .reporter {
    display: table;
    width: 100%;

    .drush-report {
      display: table-row;

      .drush-report-facet {
        display: table-cell;
        padding: 0.25em;
        background-color: rgba(255, 255, 255, 0.12);
        &:nth-child(2n+1) {
          background-color: rgba(255, 255, 255, 0.22);
        }
      }
    }
  }
}